<template>
  <div>
  	<div class=container>
			<div class="timecount">
					<div class=title>
						考核指标
					</div>
		      <vxe-table align="center" :data="timeCount" max-height='90%' auto-resize>
		      	<vxe-table-column width="26%" field="item" title="考核项目"></vxe-table-column>
		      	<vxe-table-column width="17%" field="sum" title="总工单数"></vxe-table-column>
		      	<vxe-table-column width="17%" field="timeout" title="超时数"></vxe-table-column>
		      	<vxe-table-column width="20%" field="time" title="平均历时(分钟)"></vxe-table-column>
		      	<vxe-table-column width="20%" field="rate" title="及时率"></vxe-table-column>
		    	</vxe-table>
			</div>
			
			<div class="timeout">
					<div class=title>
						超时工单
            <!-- <vxe-button id="btn_add" status="primary" icon="vxe-icon--plus" @click="isAdd = !isAdd">添加澄清</vxe-button> -->
            <!-- <vxe-modal v-model="isAdd" :lock-scroll="false" show-footer width="50vw" @confirm="parseInt(reduce[0].time) > parseInt(reduce[0].maxTime)?:addReduce"> -->
              <vxe-modal v-model="isAdd" :lock-scroll="false" show-footer width="50vw" @confirm="addReduce">
            <vxe-table
              show-overflow
              height="auto"
              align="center"
              :sync-resize="isAdd"
              border
              :data="reduce"
              :edit-config="{trigger: 'click', mode: 'cell', activeMethod: not_edit_id}">
              <vxe-table-column field="id" title="故障单编号" width="50%" :edit-render="{name: 'input'}"></vxe-table-column>
              <vxe-table-column field="province" title="澄清归属" width="30%" :edit-render="{name: 'input', attrs: {type: 'text'}}"></vxe-table-column>
              <vxe-table-column field="time" title="澄清历时" width="20%" :edit-render="{name: '$input', props: {type: 'number'}}"></vxe-table-column>
            </vxe-table>
          </vxe-modal>
					</div>
		      <vxe-table align="center" border highlight-hover-row max-height='90%' auto-resize :data="timeoutList" @cell-dblclick="dbClick">
		      	<vxe-table-column width="28%" field="id" title="故障单编号"></vxe-table-column>
		      	<vxe-table-column width="13%" field="timeLimit" title="故障时限(分钟)"></vxe-table-column>
		      	<vxe-table-column width="24%" field="timeText" title="故障历时(分钟)"></vxe-table-column>
		      	<vxe-table-column width="13%" field="mark" title="标记"></vxe-table-column>
		      	<vxe-table-column width="22%" field="reason" title="超时原因" show-overflow></vxe-table-column>
		    	</vxe-table>
			</div>
		</div>
  </div>
</template>

<script>

var HOST = "172.0.0.233/gensmo/";
export default {
  name: 'timeout',
  props:['wywData'],
  watch:{
    wywData(){
      this.rawData = this.wywData;
      this.count();
    }
  },
  data(){
    return{
    	timeCount:[
    	],
    	timeoutList:[],
    	rawData:[],
      reduce:[{id:'',province:'',time:0,maxTime:0,timeLimit:0}],
      isAdd:false
    }
  },
  created:function(){
    this.init();
  },
  methods:{
    init:function(){
    	console.log('timeout init');
  	},
  	count:function(){
  		let timeCount = [
  			{item:'TOP33',sum:0,timeout:0,rate:'100.0%',time:0,timeSum:0},
  			{item:'TOP800家1-2级',sum:0,timeout:0,rate:'100.0%',time:0,timeSum:0},
    		{item:'TOP800家3-6级',sum:0,timeout:0,rate:'100.0%',time:0,timeSum:0},
    		{item:'所有客户',sum:0,timeout:0,rate:'100.0%',time:0,timeSum:0}
  		];
      this.timeoutList = [];
  		for(let i in this.rawData){
  			let data = this.rawData[i]
  			if(data.province == '广东省广州市' && data.is_assess == '1'){
  				let timeoutFlag = data.time_out == '1';
  				timeCount[3].sum++;
  				timeCount[3].timeSum = timeCount[3].timeSum + Number(data.assessment_time);
  				timeCount[3].time = (timeCount[3].timeSum / timeCount[3].sum).toFixed(2);
  				if(timeoutFlag){
  					timeCount[3].timeout++;
  				}
  				timeCount[3].rate = Number((timeCount[3].sum - timeCount[3].timeout)/timeCount[3].sum*100).toFixed(2)+'%';
  				if(data.assess_TOPN == '1'){
  					switch(data.level){
  						case '一级':
							case '二级':
								timeCount[1].sum++;
								timeCount[1].timeSum = timeCount[1].timeSum + Number(data.assessment_time);
  							timeCount[1].time = (timeCount[1].timeSum / timeCount[1].sum).toFixed(2);
								if(timeoutFlag){
									timeCount[1].timeout++;
								}
								timeCount[1].rate = Number((timeCount[1].sum - timeCount[1].timeout)/timeCount[1].sum*100).toFixed(2)+'%';
  							break;
							case '三级':
							case '四级':
							case '五级':
							case '六级':
								timeCount[2].sum++;
								timeCount[2].timeSum = timeCount[2].timeSum + Number(data.assessment_time);
  							timeCount[2].time = (timeCount[2].timeSum / timeCount[2].sum).toFixed(2);
								if(timeoutFlag){
									timeCount[2].timeout++;
								}
								timeCount[2].rate = Number((timeCount[2].sum - timeCount[2].timeout)/timeCount[2].sum*100).toFixed(2)+'%';
  							break;
  					}
  				}
  				if(data.TOP33 == '1'){
  					timeCount[0].sum++;
	  				timeCount[0].timeSum = timeCount[0].timeSum + Number(data.assessment_time);
	  				timeCount[0].time = (timeCount[0].timeSum / timeCount[0].sum).toFixed(2);
	  				if(timeoutFlag){
	  					timeCount[0].timeout++;
	  				}
	  				timeCount[0].rate = Number((timeCount[0].sum - timeCount[0].timeout)/timeCount[0].sum*100).toFixed(2)+'%';
  				}
  				if(timeoutFlag){
  					let mark = "";
  					if(data.TOP33 == '1'){
  						mark = mark + 'TOP33 ';
  					}
  					if(data.assess_TOPN == '1'){
  						mark = mark + 'TOP800 '
  					}
  					mark = mark.slice(0,-1);
            let timeText = data.time;
            if(parseInt(data.reduce_time) > 0){
              timeText = timeText + '(-' + data.reduce_time + '=' + data.assessment_time + ')';
            }
  					let timeoutItem = {
  						id:data.orderId,
  						timeLimit:data.time_limit,
  						time:data.time,
              reduce_time:data.reduce_time,
              timeText:timeText,
  						reason:data.trouble_reason_symptom,
  						mark:mark
  					}
  					this.timeoutList.push(timeoutItem);
  				}
  			}
  		}
  		this.timeCount = timeCount;
  	},
    addReduce:function(){
      if(parseInt(this.reduce[0].time) > parseInt(this.reduce[0].maxTime)){
        this.$XModal.message({ message: '澄清时间不能大于故障历时', status: 'warning'});
        return;
      }

      let self = this;
      let data = new FormData();
      data.append('id',this.reduce[0].id);
      data.append('province',this.reduce[0].province);
      data.append('time',this.reduce[0].maxTime);
      data.append('reduce_time',this.reduce[0].time);
      data.append('time_limit',this.reduce[0].timeLimit);

      this.axios
        .post('http://'+HOST+'scripts/assess_order/add_reduce.php',data)
        .then(function(res){
          if(res.data.status == 'success'){
            if(res.data.row == '1'){
              self.$XModal.message({ message: '修改成功'});
              // for(let i in self.timeoutList){
              //   let item = self.timeoutList[i];
              //   if(item.id == self.reduce[0].id){
              //     item.reduce_time = self.reduce[0].time;
              //     if(parseInt(item.reduce_time) > 0){
              //       item.timeText = item.time + '(-' + item.reduce_time + '=' + parseInt(item.time-item.reduce_time) + ')';
              //     }
              //     self.timeoutList[i] = item;
              //     break;
              //   }
              // }
              for(let i in self.rawData){
                let data = self.rawData[i];
                if(data.orderId == self.reduce[0].id){
                  data.reduce_time = self.reduce[0].time;
                  if(self.reduce[0].province != ''){
                    data.province = self.reduce[0].province;
                  }
                  let assess_time = parseInt(self.reduce[0].maxTime) - parseInt(self.reduce[0].time);
                  data.assessment_time = assess_time;
                  if(assess_time <= self.reduce[0].timeLimit){
                    data.time_out = '0';
                  }else{
                    data.time_out = '1';
                  }
                  self.rawData[i] = data;
                  break;
                }
              }
              self.count();
            }else{
              self.$XModal.message({ message: '未找到工单或无改动', status: 'warning'});
            }
          }else{
            alert(res.data.errMsg);
          }
        })
        .catch(function(err){
          alert(err);
        });
    },
    dbClick:function(e){
      this.reduce[0].id = e.row.id;
      this.reduce[0].maxTime = e.row.time;
      this.reduce[0].timeLimit = e.row.timeLimit;
      this.isAdd = !this.isAdd;
    },
    not_edit_id ({ column, columnIndex }){
      if (columnIndex === 0) {
        return false
      }
      return true
    }
  }
}

</script>
<style scoped>
.half{
  display: inline-block;
  width:50%;
  padding:1vw;
}
.timecount{
	display: inline-block;
  width:40%;
  padding:1vw;
  height: 45vh;
}
.timeout{
	display: inline-block;
  width:60%;
  padding:1vw;
  height: 45vh;
}
.title{
	margin-bottom: 1vh;
	font-size: 18px;
	font-family: STHeiti;
	font-weight: bold;
}
#btn_add{
  margin-left: 30px;
}
.container{
	display: flex;
}
</style>